@import "settings"                                                                                                      // settings
@import "_mixins/mixins_list"                                                                                           // mixins
@import "_fonts/fonts_list"                                                                                             // fonts
@import "_fonts/fontello"                                                                                            // icons font
@import "_parts/reset"                                                                                                  // reset styles
@import "_parts/header"                                                                                                 // header

/* main page */
.main-block
  background-repeat: no-repeat
  background-position: top center
  background-size: cover
  min-height: 100vh
  background-color: #4e4846
  position: relative
  &:before
    content: ''
    display: block
    width: 100%
    height: 100%
    background-color: rgba(0,0,0,.3)
    position: absolute
    top: 0
    left: 0
  &__content
    display: flex
    flex-direction: column
    align-items: flex-start
    justify-content: flex-end
    flex-wrap: wrap
    min-height: calc(100vh - 70px)
    color: #fff
    position: relative
    z-index: 1
  &__subtitle
    font-family: Montserrat, sans-serif
    font-weight: 400
    font-size: 16px
    line-height: 20px
    display: inline-block
    margin-bottom: 10px
    @media (min-width: 415px)
      font-size: 18px
    @media (min-width: 768px)
      font-size: 20px
      margin-bottom: 15px
    @media (min-width: 960px)
      font-size: 22px
      line-height: 22px
    @media (min-width: 1200px)
      font-size: 24px
      line-height: 24px
  &__title
    font-family: Montserrat, sans-serif
    font-weight: 400
    font-size: 19px
    line-height: 24px
    display: inline-block
    margin-bottom: 10px
    @media (min-width: 340px)
      font-size: 20px
    @media (min-width: 360px)
      font-size: 21px
    @media (min-width: 414px)
      font-size: 22px
    @media (min-width: 415px)
      font-size: 24px
    @media (min-width: 768px)
      font-size: 32px
      line-height: 32px
    @media (min-width: 960px)
      font-size: 42px
      line-height: 42px
    @media (min-width: 1200px)
      font-size: 45px
      line-height: 45px
  &__descr
    font-size: 14px
    line-height: 20px
    margin-bottom: 30px
    @media (min-width: 340px)
      font-size: 15px
    @media (min-width: 360px)
      font-size: 16px
    @media (min-width: 415px)
      font-size: 18px
      font-weight: 100
      margin-top: 15px
    @media (min-width: 960px)
      font-size: 19px
      line-height: 20px
    @media (min-width: 1200px)
      margin-bottom: 50px
    p
      margin-bottom: 10px
  .enterprises-link
    display: block
    width: 100%
    border: 1px solid #aaa
    padding: 15px 0
    text-align: center
    color: #fff
    position: relative
    overflow: hidden
    transition: border-color .3s
    margin-bottom: 15px
    text-transform: uppercase
    background-color: rgba(0,0,0,.4)
    @media (min-width: 415px)
      margin-bottom: 30px
    @media (min-width: 768px)
      max-width: 300px
      margin: 0 auto 30px
    @media (min-width: 1200px)
      margin: 0 auto 50px
    span
      position: relative
      z-index: 2
    &:before
      content: ''
      display: block
      width: 100%
      height: 100%
      position: absolute
      background-color: $accent-color
      top: 0
      left: 0
      transform: translateY(100%)
      transition: transform .3s
    &:hover
      border-color: $accent-color
      &:before
        transform: translateY(0)

/* catalog page */
.catalog-header
  background-repeat: no-repeat
  background-position: center
  background-size: cover
  background-color: #4e4846
  @media (min-width: 1024px)
    padding-top: 100px !important
    padding-bottom: 20px
  &__title
    color: #fff
    font-family: Montserrat, sans-serif
    font-weight: 400
    text-transform: uppercase
    font-size: 20px
    line-height: 24px
    @media (min-width: 360px)
      font-size: 22px
      letter-spacing: .55px
    @media (min-width: 414px)
      font-size: 24px
    @media (min-width: 768px)
      font-size: 28px
      line-height: 30px
    @media (min-width: 1024px)
      font-size: 34px
      line-height: 34px
    @media (min-width: 1200px)
      font-size: 40px
      line-height: 40px
  .search-form
    margin: 10px 0 15px
    @media (min-width: 360px)
      margin: 20px 0
    .input-group
      background-color: rgba(255,255,255,.9)
      display: flex
      button
        display: flex
        align-items: center
        justify-content: center
        border: none
        background-color: transparent
        font-size: 28px
        line-height: 20px
        padding: 0 10px
      input
        flex: 1 1 100%
        border: none
        border-radius: 0
        background-color: transparent
        padding: 10px
        font-size: 18px
        line-height: 20px
        font-weight: 100
        @include input-placeholder
          opacity: 1
          transition: opacity 0.3s ease
        &:focus
          @include input-placeholder
            opacity: 0
            transition: opacity 0.3s ease
.catalog-menu
  background-color: $txt-color
  padding: 15px 0
  color: #999
  @media (min-width: 1024px)
    padding: 8px 0
  .catalog-link
    display: flex
    flex: 1 1 100%
    text-transform: uppercase
    font-family: Montserrat, sans-serif
    font-size: 18px
    line-height: 20px
    letter-spacing: .5px
    color: #fff
    @media (min-width: 1024px)
      display: none
    .icon
      margin-right: 15px
      font-size: 23px
      line-height: 20px
      &.icon-down-open-mini
        margin: 0 0 0 auto
        font-size: 24px
        line-height: 20px
        transition: transform .3s
    &.open
      .icon.icon-down-open-mini
        transform: scale(1,-1)
  &__list
    @media (min-width: 1024px)
      display: flex !important
      flex-wrap: wrap
      justify-content: space-around
  &__item
    padding: 8px 10px
    border-bottom: 1px solid #222
    @media (min-width: 1024px)
      border-bottom: none
    &:nth-of-type(1)
      margin-top: 15px
      @media (min-width: 1024px)
        margin-top: 0
    &:last-child
      border-bottom: none
      color: $accent-color
  &__link
    display: block
    font-family: Montserrat, sans-serif
    transition: color .3s
    font-size: 16px
    line-height: 20px
    &:hover
      color: #fff

.catalog
  &__title
    @media (min-width: 1024px)
      flex: 1 1 100%
  &__content
    margin-bottom: 30px
    @media (min-width: 1024px)
      display: flex
      flex-wrap: wrap
      align-items: stretch
    &__left-side
      margin: 15px 0
      border: 1px solid #aaa
      @media (min-width: 1024px)
        width: 100%
        max-width: 230px
        flex: 0 0 230px
        border: none
        border-right: 1px solid #eee
        margin: 0 20px 0 0
      .filter-link
        display: flex
        flex: 1 1 100%
        padding: 10px 14px
        font-size: 18px
        line-height: 20px
        @media (min-width: 1024px)
          display: none
        .icon
          margin-right: 10px
          font-size: 24px
          line-height: 20px
          &.icon-down-open-mini
            margin: 0 0 0 auto
            font-size: 24px
            line-height: 20px
            transform: scale(1,1)
            transition: transform .3s
        &.open
          .icon-down-open-mini
            transform: scale(1,-1)
      .product-filters-block
        border-top: 1px solid #aaa
        @media (min-width: 1024px)
          display: block !important
          border-top: none
          padding: 0 20px 0 0
    &__right-side
      @media (min-width: 1024px)
        flex: 1 1 calc(100% - 250px)
        max-width: calc(100% - 250px)
        .catalog__title
          margin-bottom: 10px
      .category-menu
        margin: 15px 0
        &__list
          position: relative
          padding: 0 20px
          .slide-item
            text-align: center
            .item-icon
              display: flex
              width: 70px
              height: 70px
              align-items: center
              justify-content: center
              margin: 0 auto
            .item-title
              font-size: 14px
              line-height: 14px
              text-align: center
          .owl-nav
            display: flex
            width: 100%
            position: absolute
            top: calc(50% - 12.5px)
            left: 0
            justify-content: space-between
            font-size: 25px
            line-height: 25px
            height: 0px
        &__link
          display: flex
          flex-wrap: wrap
          align-items: center
          justify-content: center
          flex-direction: column
          .item-title
            color: $txt-color
            transition: color .3s
          &:hover
            .item-title
              color: $accent-color
      .catalog__filter
        margin: 15px 0
        @media (min-width: 640px)
          display: flex
          align-items: center
          justify-content: space-between
          border-bottom: 1px solid #eee
          padding-bottom: 15px
          margin-top: 20px
        .filter-name
          display: inline-block
          font-size: 15px
          line-height: 20px
          margin-bottom: 5px
        &__sort
          margin-bottom: 20px
          @media (min-width: 640px)
            flex: 1 1 auto
            margin-bottom: 0
          .sort-form
            display: flex
            flex-wrap: wrap
            .filter-name
              display: block
              flex: 1 1 100%
              @media (min-width: 640px)
                display: inline-block
                flex: 0 1 auto
                margin-right: 15px
                margin-bottom: 0
            .input-group
              flex: 0 1 auto
              margin-left: 15px
              position: relative
              @media (min-width: 340px)
                margin-left: 24px
              &:before
                content: ''
                display: block
                width: 1px
                height: 100%
                position: absolute
                left: -8px
                background-color: #aaa
                @media (min-width: 340px)
                  left: -14px
              &:nth-of-type(1)
                margin-left: 0
                &:before
                  display: none
          label
            display: inline-block
            font-size: 14px
            line-height: 20px
            @media (min-width: 640px)
              font-size: 15px
            span
              display: flex
              align-items: center
              white-space: nowrap
              transition: color .3s
            input
              position: absolute
              left: 0
              top: 0
              width: 0
              height: 0
              visibility: hidden
              &:checked + span
                color: $accent-color
              &:checked[data-type='ASC'] + span
                &:before
                  font-family: Fontello
                  font-style: normal
                  font-weight: normal
                  speak: never
                  display: inline-block
                  margin: 0 5px 0 0
                  text-decoration: inherit
                  text-align: center
                  font-variant: normal
                  text-transform: none
                  content: '\f161'
              &:checked[data-type='DESC'] + span
                &:before
                  font-family: Fontello
                  font-style: normal
                  font-weight: normal
                  speak: never
                  display: inline-block
                  margin: 0 5px 0 0
                  text-decoration: inherit
                  text-align: center
                  font-variant: normal
                  text-transform: none
                  content: '\f160'
        &__view
          display: flex
          align-items: center
          @media (min-width: 640px)
            flex: 1 1 auto
            justify-content: flex-end
          .filter-name
            margin-bottom: 0
            margin-right: 10px
            @media (min-width: 640px)
              display: none
          .catalog-view-type
            font-size: 20px
            line-height: 20px
            display: inline-block
            margin-left: 10px
            color: #999
            transition: color .3s
            &.active
              color: $accent-color
            &:nth-of-type(1)
              margin-left: 0
      .catalog-list
        margin-top: 30px
        display: flex
        align-items: stretch
        flex-wrap: wrap
        flex: 1 1 100%
        @media (min-width: 640px)
          margin-top: 15px
        .catalog-list__item
          flex: 0 1 calc(50% - 10px)
          width: calc(50% - 10px)
          margin: 0 20px 30px 0
          display: flex
          flex-direction: column
          align-items: stretch
          justify-content: stretch
          &:nth-of-type(2n)
            margin-right: 0
          &:hover
            .descr-block
              .item-name
                color: $accent-color
          @media (min-width: 576px)
            flex: 0 1 calc(33% - 10px)
            width: calc(33% - 10px)
            margin-right: 17px
            &:nth-of-type(2n)
              margin-right: 17px
            &:nth-of-type(3n)
              margin-right: 0
          @media (min-width: 768px)
            flex: 0 1 calc(25% - 10px)
            width: calc(25% - 10px)
            margin-right: 13px
            &:nth-of-type(2n)
              margin-right: 13px
            &:nth-of-type(3n)
              margin-right: 13px
            &:nth-of-type(4n)
              margin-right: 0
          @media (min-width: 1200px)
            flex: 0 1 calc(20% - 11px)
            width: calc(25% - 11px)
            margin-right: 13px
            &:nth-of-type(2n)
              margin-right: 13px
            &:nth-of-type(3n)
              margin-right: 13px
            &:nth-of-type(4n)
              margin-right: 13px
            &:nth-of-type(5n)
              margin-right: 0
          .image-block
            flex: 0 0 auto
            .item-link
              display: flex
              align-items: center
              justify-content: center
            .item-image
              display: block
              margin: 0 auto
              max-width: 100%
              @media (min-width: 360px)
                width: 100%
          .descr-block
            margin-top: 10px
            flex: 1 1 100%
            display: flex
            flex-direction: column
            align-items: flex-start
            justify-content: space-between
            .price-block
              display: flex
              flex-wrap: wrap
              .item-price
                display: inline-block
                color: #1a1a1a
                font-weight: 700
                font-size: 18px
                line-height: 20px
              .item-oldprice
                display: inline-block
                color: #aaa
                font-weight: 700
                font-size: 18px
                line-height: 20px
                text-decoration: line-through
                margin-right: 15px
              .item-oldprice + .item-price
                color: $accent-color
            .item-name
              display: block
              margin-top: 10px
              transition: color .3s
              &:hover
                color: $accent-color
            .addcart-form
              margin-top: 15px
              width: 100%
              .input-group
                display: flex
                align-items: stretch
                width: 100%
                &.count-group
                  margin-bottom: 8px
                .count-btn
                  display: inline-flex
                  align-items: center
                  justify-content: center
                  width: 30px
                  height: 30px
                  flex: 0 0 30px
                  border: 1px solid #aaa
                  transition: border-color .3s, color .3s
                  &:hover
                    border-color: $accent-color
                    color: $accent-color
                .count-input
                  display: inline-block
                  flex: 1 1 auto
                  width: 100%
                  border: none
                  border-top: 1px solid #aaa
                  border-bottom: 1px solid #aaa
                  text-align: center
                  background-color: transparent
                .add-cart-btn
                  display: block
                  flex: 1 1 100%
                  width: 100%
                  padding: 10px
                  background-color: transparent
                  color: #1a1a1a
                  font-size: 15px
                  margin: 0
                  &:hover
                    color: #fff

/* product page */
.product
  &__title
    @media (min-width: 576px)
      flex: 1 1 100%
  &__content
    @media (min-width: 576px)
      display: flex
      flex-wrap: wrap
    .product-comments
      display: flex
      flex-wrap: wrap
      margin: 10px 0
      @media (min-width: 576px)
        flex: 1 1 100%
    .product-rating
      display: flex
      color: #ffcb00
      @media (min-width: 1200px)
        font-size: 16px
        line-height: 20px
      .icon
        display: inline-block
        margin-right: 3px
        @media (min-width: 1200px)
          margin-right: 4px
        &:last-child
          margin-right: 0
    .product-answers,.product-questions
      display: inline-block
      color: $accent-color
      transition: color .3s
    .product-answers
      margin: 0 10px
      @media (min-width: 1200px)
        margin: 0 20px
    &__left-side
      @media (min-width: 576px)
        flex: 1 1 100%
        width: 100%
      @media (min-width: 768px)
        flex: 1 1 auto
        width: auto
        max-width: 480px
        margin-top: 15px
        position: relative
        &:after
          content: ''
          display: block
          width: 1px
          height: 100%
          background-color: #eee
          position: absolute
          top: 0
          right: 15px
      @media (min-width: 1024px)
        display: flex
        align-items: stretch
        width: auto
        flex: 0 1 auto
        max-width: calc(100% - 245px)
        &:after
          display: none
      .slider-block
        display: flex
        flex-wrap: wrap
        margin: 25px 0 15px
        @media (min-width: 380px)
          max-width: 380px
          margin: 25px auto 15px
        @media (min-width: 576px)
          flex-wrap: nowrap
          max-width: 480px
          margin: 25px 0 15px
        @media (min-width: 768px)
          margin-top: 0
        @media (min-width: 1024px)
          max-width: 450px
          margin-right: 10px
        .slick-thumbs
          flex: 1 1 100%
          order: 2
          width: 100%
          max-width: 100%
          padding: 0 25px
          position: relative
          margin-top: 15px
          @media (min-width: 576px)
            flex: 0 0 60px
            order: 1
            margin: 0 10px 0 0
            padding: 32px 0
            max-height: 380px
          @media (min-width: 1200px)
            margin-right: 20px
          .slide-image
            margin: 0 auto
            max-width: 100%
            cursor: pointer
          .icon
            position: absolute
            font-size: 25px
            line-height: 30px
            left: 0
            top: calc(50% - 16px)
            cursor: pointer
            @media (min-width: 576px)
              top: -5px
              left: calc(50% - 6px)
              transform: rotate(90deg)
              height: 30px
              width: 12px
            &.next-btn
              left: auto
              right: 0
              @media (min-width: 576px)
                right: auto
                left: calc(50% - 6px)
                top: auto
                bottom: -5px
        .slick-main
          flex: 1 1 100%
          order: 1
          width: 100%
          max-width: 100%
          @media (min-width: 576px)
            flex: 1 1 calc(100% - 100px)
            max-width: calc(100% - 100px)
            order: 2
          @media (min-width: 1024px)
            flex: 1 1 calc(100% - 70px)
            max-width: calc(100% - 70px)
      .descr-block
        @media (min-width: 1024px)
          display: block !important
          min-width: 230px
          margin: 0 15px
          font-size: 15px
          line-height: 20px
          .descr-list
            &__item
              display: flex
              flex-wrap: wrap
              justify-content: space-between
              border-bottom: 2px dotted #999
              margin-bottom: 15px
              &:last-child
                margin-bottom: 0
              span
                display: inline-block
                background-color: #fff
                margin-bottom: -7px
                &:nth-of-type(1)
                  padding-right: 5px
                  color: #999
                &:nth-of-type(2)
                  padding-left: 5px
          .more-descr-link
            display: inline-block
            margin-top: 25px
            color: $accent-color
        @media (min-width: 1200px)
          margin: 0 30px
          min-width: 290px
    &__right-side
      padding: 15px 0
      border-top: 1px solid #eee
      @media (min-width: 576px)
        flex: 1 1 100%
        width: 100%
      @media (min-width: 768px)
        flex: 1 1 calc(100% -  480px)
        max-width: calc(100% - 480px)
        margin-top: 15px
        border-top: none
        padding: 0
      @media (min-width: 1024px)
        flex: 0 1 auto
        max-width: initial
        width: auto
        margin: 15px auto 0 0
        padding-left: 15px
        position: relative
        &:after
          content: ''
          display: block
          width: 1px
          height: 100%
          background-color: #eee
          position: absolute
          top: 0
          left: 0
      @media (min-width: 1200px)
        padding-left: 30px
      .price-block
        display: flex
        flex-wrap: wrap
        .price
          display: inline-block
          color: $accent-color
          font-size: 24px
          line-height: 24px
          font-weight: 700
        .old-price
          display: inline-block
          color: $txt-color
          text-decoration: line-through
          font-size: 24px
          line-height: 24px
          font-weight: 500
          margin-left: 15px
      .properties-block
        margin: 10px 0
        &__item
          margin-bottom: 15px
          &:last-child
            margin-bottom: 0
        .property__title
          display: flex
          color: #999
          .property-value
            display: inline-block
            margin-left: 10px
            color: $txt-color
        .property__list
          display: flex
          flex-wrap: wrap
          margin-top: 10px
          .input-group
            flex: 0 0 auto
            margin: 5px
            &:nth-of-type(1)
              margin-left: 0
            label
              display: flex
              align-items: center
              justify-content: center
              position: relative
              padding: 4px
              min-width: 30px
              min-height: 30px
              input
                position: absolute
                top: 0
                left: 0
                visibility: hidden
                opacity: 0
              img
                display: block
              .border-line
                position: absolute
                top: 0
                left: 0
                width: 100%
                height: 100%
                border: 1px solid #eee
                transition: border-color .3s
              input:checked + .border-line
                border-color: $accent-color
              input:disabled + .border-line
                border-color: rgba(#e6e6e6, .3)
                background-color: rgba(#e6e6e6, .3)
                z-index: 1
      .addcart-block
        margin: 15px 0
        .addcart-form
          .input-group
            display: flex
            align-items: stretch
            &.count-group
              display: flex
              align-items: stretch
              margin-bottom: 10px
            .count-btn
              display: inline-flex
              align-items: center
              justify-content: center
              width: 30px
              height: 30px
              flex: 0 0 30px
              border: 1px solid #aaa
              transition: border-color .3s, color .3s
              &:hover
                border-color: $accent-color
                color: $accent-color
            .count-input
              display: inline-block
              flex: 0 1 auto
              max-width: 40px
              width: auto
              border: none
              border-top: 1px solid #aaa
              border-bottom: 1px solid #aaa
              text-align: center
              background-color: transparent
            .add-cart-btn
              display: block
              flex: 0 1 auto
              width: auto
              padding: 10px 20px
              background-color: transparent
              color: #1a1a1a
              font-size: 15px
              margin: 0
              &:hover
                color: #fff
            .addfavorite-link
              display: flex
              align-items: center
              justify-content: center
              flex: 0 0 42px
              width: 42px
              height: 42px
              font-size: 20px
              line-height: 20px
              margin-left: 10px
              color: $accent-color
              &.active
                .icon-heart-empty:before
                  content: '\e803'
      .seller-info-block
        display: flex
        flex-direction: column
        align-items: flex-start
        justify-content: flex-start
        flex-wrap: wrap
        border-top: 1px solid #eee
        padding-top: 15px
        color: #999
        @media (min-width: 768px)
          border-top: none
        & > span
          margin-bottom: 10px
        span > span
          color: $txt-color
          display: inline-block
          margin-left: 5px
        span > a
          color: $accent-color
          text-decoration: none
          display: inline-block
          margin-left: 5px
        .seller-icon
          display: block
          width: auto
          max-width: 130px
          @media (min-width: 768px)
            display: none
    &__full
      padding-top: 10px
      @media (min-width: 960px)
        margin-top: 20px
      .descr-row
        margin-bottom: 30px
        &:last-child
          margin-bottom: 0
        &__title
          text-transform: uppercase
          color: #999
          font-size: 22px
          line-height: 22px
          font-weight: 400
          display: block
          width: 100%
          border-bottom: 1px solid #eee
          padding-bottom: 5px
          margin-bottom: 10px
        &__content
          font-size: 15px
          line-height: 20px
          @media (min-width: 768px)
            display: flex
            flex-wrap: wrap
          @media (min-width: 1200px)
            font-size: 16px
          .properties__column
            margin-bottom: 35px
            @media (min-width: 768px)
              flex: 1 1 calc(50% - 15px)
              max-width: calc(50% - 15px)
              margin-right: 30px
              &:nth-of-type(2n)
                margin-right: 0
            @media (min-width: 960px)
              flex: 1 1 calc(33% - 15px)
              max-width: calc(33% - 15px)
              margin-right: 25px
              &:nth-of-type(2n)
                margin-right: 25px
              &:nth-of-type(3n)
                margin-right: 0
            .descr-list
              &__item
                display: flex
                flex-wrap: wrap
                justify-content: space-between
                border-bottom: 2px dotted #999
                margin-bottom: 15px
                &:last-child
                  margin-bottom: 0
                span
                  display: inline-block
                  background-color: #fff
                  margin-bottom: -7px
                  &:nth-of-type(1)
                    padding-right: 5px
                    color: #999
                  &:nth-of-type(2)
                    padding-left: 5px

/* stocks page */
.stocks-header
  background-repeat: no-repeat
  background-position: top center
  background-size: cover
  min-height: 100vh
  background-color: #4e4846
  position: relative
  &:before
    content: ''
    display: block
    width: 100%
    height: 100%
    background-color: rgba(0,0,0,.3)
    position: absolute
    top: 0
    left: 0
  &__content
    display: flex
    flex-direction: column
    align-items: flex-start
    justify-content: flex-end
    flex-wrap: wrap
    min-height: calc(100vh - 70px)
    color: #fff
    position: relative
    z-index: 1
    @media (min-width: 768px)
      padding-bottom: 60px
  &__title
    text-transform: uppercase
    font-family: Montserrat, sans-serif
    font-weight: 400
    font-size: 22px
    line-height: 24px
    display: inline-block
    margin-bottom: 10px
    @media (min-width: 360px)
      font-size: 24px
    @media (min-width: 768px)
      font-size: 32px
      line-height: 32px
    @media (min-width: 960px)
      font-size: 42px
      line-height: 42px
    @media (min-width: 1200px)
      font-size: 45px
      line-height: 45px
  &__descr
    font-size: 15px
    line-height: 22px
    margin-bottom: 30px
    @media (min-width: 360px)
      font-size: 16px
    @media (min-width: 415px)
      font-size: 18px
      font-weight: 100
      margin-top: 15px
    @media (min-width: 960px)
      font-size: 19px
      line-height: 24px
    @media (min-width: 1200px)
      margin-bottom: 50px
    p
      margin-bottom: 10px

/* contacts page */
.contacts
  background-repeat: no-repeat
  background-position: top center
  background-size: cover
  background-color: #4e4846
  position: relative
  &:before
    content: ''
    display: block
    width: 100%
    height: 100%
    background-color: rgba(0,0,0,.3)
    position: absolute
    top: 0
    left: 0
  &__content
    position: relative
    z-index: 1
    color: #fff
    @media (min-width: 1200px)
      margin-top: 100px
  .contact-title
    text-transform: uppercase
    font-family: Montserrat, sans-serif
    font-weight: 400
    font-size: 25px
    line-height: 24px
    @media (min-width: 576px)
      font-size: 30px
      line-height: 35px
    @media (min-width: 768px)
      font-size: 35px
      line-height: 40px
    @media (min-width: 1200px)
      font-size: 42px
      line-height: 55px
  &__links
    margin: 30px 0
    font-size: 18px
    line-height: 20px
    @media (min-width: 1200px)
      font-size: 20px
      line-height: 24px
    p
      margin-bottom: 10px
      &:last-child
        margin-bottom: 0
      a
        display: inline-block
        margin-left: 10px
        transition: color .3s
        &:hover
          color: $accent-color
  &__form
    margin: 35px 0
    @media (min-width: 1200px)
      margin: 80px 0 0 0
    form
      margin-top: 20px
      @media (min-width: 576px)
        max-width: 420px
      @media (min-width: 768px)
        max-width: 100%
        label
          font-size: 18px
      .input-row
        flex-direction: column
        @media (min-width: 768px)
          flex-direction: row
          .input-group
            flex: 1 1 auto
            margin: 20px 10px
            @media (min-width: 1200px)
              margin: 30px 20px
      .input-group
        @media (min-width: 768px)
          margin: 20px 10px
        @media (min-width: 1200px)
          margin: 30px 20px
      .input-group.checkgroup
        @media (min-width: 768px)
          margin: 40px 10px
        @media (min-width: 1200px)
          margin: 40px 20px
      .input-group.btn-group
        @media (min-width: 768px)
          button
            max-width: 320px
            margin: 0 auto
        @media (min-width: 1200px)
          margin-top: 60px

/* enterprises page */
.enterprises-header
  background-repeat: no-repeat
  background-position: center
  background-size: cover
  background-color: #4e4846
  min-height: 100vh
  @media (min-width: 1024px)
    padding-top: 100px !important
    padding-bottom: 20px
  &__content
    display: flex
    flex-wrap: wrap
    flex-direction: column
    align-items: flex-start
    justify-content: flex-end
  &__title
    flex: 1 1 100%
    color: #fff
    font-family: Montserrat, sans-serif
    font-weight: 400
    text-transform: uppercase
    font-size: 20px
    line-height: 24px
    @media (min-width: 360px)
      font-size: 22px
      letter-spacing: .55px
    @media (min-width: 414px)
      font-size: 24px
    @media (min-width: 768px)
      font-size: 28px
      line-height: 30px
    @media (min-width: 1024px)
      font-size: 34px
      line-height: 34px
    @media (min-width: 1200px)
      font-size: 40px
      line-height: 40px
  .search-form
    margin: 10px 0 15px
    @media (min-width: 360px)
      margin: 20px 0
    .input-group
      background-color: rgba(255,255,255,.9)
      display: flex
      button
        display: flex
        align-items: center
        justify-content: center
        border: none
        background-color: transparent
        font-size: 28px
        line-height: 20px
        padding: 0 10px
      input
        flex: 1 1 100%
        border: none
        border-radius: 0
        background-color: transparent
        padding: 10px
        font-size: 18px
        line-height: 20px
        font-weight: 100
        @include input-placeholder
          opacity: 1
          transition: opacity 0.3s ease
        &:focus
          @include input-placeholder
            opacity: 0
            transition: opacity 0.3s ease





















